import { useState } from "react";

function App() {
  const [count, setCounter] = useState({
    run: 0,
    wicket: 0,
    over: 0
  });
  const runHandle = (value, type) => {
    if (type === "one") {
      setCounter({ ...count, run: count.run + value });
    } else if (type === "four") {
      setCounter({ ...count, run: count.run + value });
    } else {
      setCounter({ ...count, run: count.run + value });
    }
  };
  const wicketHandle = (value) => {
    if (count.wicket <= 12) {
      setCounter({ ...count, wicket: count.wicket + value });
    }
    return;
  };
  const overHandle = (value) => {
    if (count.over < 10) {
      setCounter({ ...count, over: count.over + value });
    }
  };

  return (
    <div className="App">
      <h3>India:</h3>
      <div className="banner">
        <div>
          Score:{count.run}
          <h1 className="scoreCount">
            {
              // show "score" here
              // <span>{count.run}</span>
            }
          </h1>
        </div>
        <div>
          Wicket:{count.wicket}
          <h1 className="wicketCount">
            {
              // show wicket here
            }
          </h1>
        </div>

        <div>
          Over:{count.over}
          <h1 className="overCount">
            {
              // Show Over here in the format: "over.ball" eg: 4.5 means 4th over and 5th ball
              // if 1 more ball is thrown then over is now 5.0
              // you have to write logic to form this string from current ball number.
            }
          </h1>
        </div>
      </div>

      <div className="addScore">
        Add Score
        {/* these buttons should add the respective amount in the score */}
        <button
          className="addScore1"
          onClick={() => {
            runHandle(1, "one");
          }}
        >
          Add 1
        </button>
        <button
          className="addScore4"
          onClick={() => {
            runHandle(4, "four");
          }}
        >
          Add 4
        </button>
        <button
          className="addScore6"
          onClick={() => {
            runHandle(6, "six");
          }}
        >
          Add 6
        </button>
      </div>

      <div className="addWicket">
        Add Wicket
        {/* Increase the count of wicket */}
        <button>Add 1 wicket</button>
      </div>

      <div className="addBall">
        Add ball
        {/* Increase the total number of balls thrown here. */}
        <button>Add 1</button>
      </div>

      {/* If score reaches greater than 100, show text "India Won" without quotes in h1 tag with class name 'status' */}
      <h1 className="status">{runHandle}</h1>
    </div>
  );
}

export default App;
